import { deepMerge, isFunction } from "@/utils";
import { renderNode } from "@/utils/vnode";
import Form from "@/utils/form";
import Parse from "@/utils/parse";
import { __inst } from "@/options";
import Emitter from "@/mixins/emitter";
import cloneDeep from "clone-deep";

export default {
	name: "cl-form-base",
	componentName: "ClFormBase",
	mixins: [Emitter],
	props: {
		// Bind value
		value: {
			type: Object,
			default: () => {
				return {};
			}
		},
		options:{
			type: Object,
			default: () => {
				return {};
			}
		}
	},
	data() {
		return {
			saving: false,
			loading: false,
			form: {}, // 

			conf: {
				on: {
					open: null,
					submit: null,
					close: null
				},
				props: {
					fullscreen: false,
					"append-to-body": true,
					"close-on-click-modal": false,
					"destroy-on-close": true
				},
				op: {
					hidden: false,
					confirmButtonText: "",
					cancelButtonText: "",
					layout: ["cancel", "confirm"]
				},
				items: [],
				_data: {}
			}
		};
	},
	watch: {
		options:{
			immediate: true,
			handler(val) {
				//console.log('watch options val:', val)

				// Merge conf
				for (let i in this.conf) {
					if (i == "items") {
						this.conf.items = cloneDeep(val[i] || []);
					} else {
						deepMerge(this.conf[i], val[i]);
					}
				}

				//console.log('this.conf:', this.conf.items)

				// Set Form
				this.conf.items.map((e) => {
					if (e.prop) {
						// Priority use form data
						this.$set(this.form, e.prop, this.form[e.prop] || cloneDeep(e.value));
					}
				});

				//console.log('this.form:', this.form)

				// Open callback
				const { open } = this.conf.on;

				if (open) {
					this.$nextTick(() => {
						open(this.form, { close: this.close, submit: this.submit, done: this.done });
					});
				}
			}
		},
		value: {
			// immediate: true,
			deep: true,
			handler(val) {
				//console.log('this.form:',val)
				this.form = val
			}
		},
		form: {
			immediate: true,
			handler(val) {
				this.$emit("input", val);
			}
		}
	},
	created() {
		Form.inject.call(this, {
			form: this.form,
			scope: "conf."
		});
	},
	methods: {

		close(action = "close") {
			// Close event
			const done = () => {
				this._close()
			};

			// Hooks event
			if (this.conf.on.close) {
				this.conf.on.close(action, done)
			} else {
				done()
			}
		},

		_close() {
			this.visible = false;
			this.clear();
			this.done();
		},

		done() {
			this.saving = false;
		},

		clear() {
			this.clearForm();
		},

		submit() {
			// Validate form
			this.$refs["form"].validate(async (valid) => {
				if (valid) {
					this.saving = true;

					// Hooks event
					const { submit } = this.conf.on;

					// Get mount variable
					const { $refs } = __inst;

					// Hooks by onSubmit
					if (isFunction(submit)) {
						submit(this.form, {
							done: this.done,
							close: () => {
								this.close("submit");
							},
							$refs
						});
					} else {
						console.error("on[submit] is not found");
					}
				}
			});
		},

		showLoading() {
			this.loading = true;
		},

		hiddenLoading() {
			this.loading = false;
		},

		collapseItem(item) {
			if (item.collapse !== undefined) {
				item.collapse = !item.collapse;
			}
		},

		formRender() {
			const { props, items } = this.conf;

			return (
				<el-form
					ref="form"
					class="cl-form"
					{...{
						props: {
							size: "small",
							"label-width": "100px",
							disabled: this.saving,
							model: this.form,
							...props
						}
					}}>
					<el-row gutter={10} v-loading={this.loading}>
						{items.map((e, i) => {
							return (
								!Parse("hidden", {
									value: e.hidden,
									scope: this.form,
									data: this.conf._data
								}) && (
									<el-col
										key={`form-item-${i}`}
										{...{
											props: {
												key: i,
												span: 24,
												...e
											}
										}}>
										{e.component && (
											<el-form-item
												{...{
													props: {
														label: e.label,
														prop: e.prop,
														rules: e.rules,
														...e.props
													}
												}}>
												{/* Redefine label */}
												<template slot="label">
													<span
														on-click={() => {
															this.collapseItem(e);
														}}>
														{e.label}
													</span>
												</template>

												{/* Form item */}
												<div class="cl-form-item">
													{/* Component */}
													{["prepend", "component", "append"].map(
														(name) => {
															return (
																e[name] && (
																	<div
																		class={`cl-form-item__${name}`}
																		v-show={!e.collapse}>
																		{renderNode(e[name], {
																			prop: e.prop,
																			scope: this.form,
																			$scopedSlots: this
																				.$scopedSlots
																		})}
																	</div>
																)
															);
														}
													)}

													{/* Collapse button */}
													<div
														class="cl-form-item__collapse"
														v-show={e.collapse}
														on-click={() => {
															this.collapseItem(e);
														}}>
														<el-divider content-position="center">
															
															<i class="el-icon-arrow-down"></i>
														</el-divider>
													</div>
												</div>
											</el-form-item>
										)}
									</el-col>
								)
							);
						})}
					</el-row>
				</el-form>
			);
		},

		footerRender() {
			const { hidden, layout, confirmButtonText, cancelButtonText } = this.conf.op;
			const { size = "small" } = this.conf.props;

			return (
				<div class="cl-form__footer" style="justify-content: flex-start">
					{!hidden &&
						layout.map((vnode) => {
							if (vnode == "confirm") {
								return (
									<el-button
										{...{
											props: {
												size,
												type: "success",
												disabled: this.loading,
												loading: this.saving
											},
											on: {
												click: this.submit
											}
										}}>
										{confirmButtonText}
									</el-button>
								);
							} else if (vnode == "cancel") {
								return (
									<el-button
										{...{
											props: {
												size
											},
											on: {
												click: () => {
													this.close("cancel");
												}
											}
										}}>
										{cancelButtonText}
									</el-button>
								);
							} else {
								return renderNode(vnode, {
									scope: this.form,
									$scopedSlots: this.$scopedSlots
								});
							}
						})}
				</div>
			);
		}
	},

	// cl-dialog
	render() {

		return (
			<div class="cl-form">
				<div class="cl-form__container">{this.formRender()}</div>
				{this.footerRender()}
			</div>
		)
		
	}
};
